.Login{
    display: flex;
    width: 100%;
    min-height: calc(100vh - 10rem);
    align-items: center;
    justify-content: center;
}

.button{
    padding: 1rem;
    font-size: 2rem;
    font-weight: 700;
    color: white;
    background-color: black;
    text-decoration: none;
}

.button:hover{
    background-color: rgb(90, 0, 0);
}